@font-face {
	font-family: 'Oswald';
	src: url('../fonts/oswald_medium.eot'); /* IE 9 Compatibility Mode */
	src: url('../fonts/oswald_medium.eot?#iefix') format('embedded-opentype'), /* IE < 9 */
		url('../fonts/oswald_medium.woff2') format('woff2'), /* Super Modern Browsers */
		url('../fonts/oswald_medium.woff') format('woff'), /* Firefox >= 3.6, any other modern browser */
		url('../fonts/oswald_medium.ttf') format('truetype'), /* Safari, Android, iOS */
		url('../fonts/oswald_medium.svg#oswald_medium') format('svg'); /* Chrome < 4, Legacy iOS */
}

