@charset "UTF-8";
.advantages__title {
  font-size: 36px;
  line-height: 36px;
  text-transform: uppercase;
  font-weight: 400;
}

.advantages__subtitle {
  font-family: "Arial";
}

.adavantages__catalog {
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
}

.food__title {
  font-size: 60px;
  line-height: 60px;
}

.food__subtitle {
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.btn {
  color: var(--basic_white);
  background-color: var(--basic_green);
}

.food__btn {
  padding: 14px 26px;
}

.main-nav__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  padding: 0;
}

.main-nav__item {
  color: green;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  padding-right: 31px;
}

.page-body {
  min-width: 1220px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

.wrapper {
  width: 1220px;
  margin: 0 auto;
  text-align: center;
}

.page-header__btn-toogle {
  display: none;
}

.page-header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-top: 53px;
}

.works__title {
  font-size: 60px;
  line-height: 60px;
  font-weight: 400;
}

.catalog__title {
  font-size: 60px;
  line-height: 60px;
  font-weight: 400;
}

.catalog__item__title {
  font-size: 20px;
  line-height: 21px;
  text-transform: uppercase;
  font-weight: 400;
}

.catalog__item__subtitle {
  font-size: 16ox;
  line-height: 16px;
  font-family: "Arial", sans-serif;
  color: var(--special_dark-grey);
}

.catalog__item__btn {
  padding: 10px 50px;
}

.catalog__item__span {
  font-size: 16px;
  line-height: 18px;
  font-family: "Arial", sans-serif;
  color: var(--special_dark-grey);
}

.catalog__item__btn_grey {
  background-color: var(--basic_grey);
  color: black;
  text-transform: uppercase;
  padding: 20px 40px;
}

